<template>
  <div>
    <el-upload
      :disabled="this.disabled"
      class="avatar-uploader"
      action="/api/sysAttach/uploadFile/image/test"
      :show-file-list="false"
      :on-success="handleAvatarSuccess"
      :before-upload="beforeAvatarUpload">
      <img v-if="url" :src="this.imgUrl" class="avatar">
      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload>
  </div>
</template>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>

<script>
  export default {
    props: {
      url: {
        type: String,
        default: ''
      },
      style: {
        type: String,
        default: ''
      },
      disabled: {
        type: Boolean,
        default:false
      }
    },
    data() {
      return {
        imgUrl:"",
      }
    },
    watch: {
      url(url){
        this.imgUrl = this.formatUrl(this.url)
      }
    },
    mounted() {
      this.imgUrl = this.formatUrl(this.url)
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.$emit('refreshPhoto',res.obj.savePath);
        this.url = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isPng = file.type === 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!(isJPG||isPng)) {
          this.$message.error('上传头像图片只能是 jpg或者png 格式!');
          return false
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
          return false
        }
        return true;
      }
    }
  }
</script>



